import React from 'react';
import axios from '../services/axios/axios';
import headers from '../services/axios/headers';

import * as actionType from '../constants/actionTypes';

export const getBlogs3 = (callback) => {
    axios.get(`/blogs/`, { headers, crossdomain: true })
        .then((res) => {
            callback(res)
            console.log("=====>", res);
        })
        .catch(err => {
            callback({
                status: 'error',
                data: err
            });
        });
};

export const deleteBlog5 = (callback, id) => {
    axios.delete(`/blogs/${id}/`, {
            headers: {
                ...headers,
                'Content-Type': 'application/json'
            }
        })
        .then(res => {
            //console.log(res);
            callback(res)
        })
        .catch(err => callback(err))
};

export const fetchBlogs3 = (data) =>async (dispatch) => {
    dispatch({
        type: actionType.BLOG_ITEM,
        value: data
    })
}
export const pubunpubBlog3 = (data) => async (dispatch) =>{
    dispatch({
        type: actionType.UNPUBLISHED_BLOG,
        value: data
    });
}
export const deleteBlog6 = (data) =>async (dispatch) => {
    dispatch({
        type: actionType.DELETE_BLOG,
        value: data
    });
}