import React, { Component, useEffect, useState } from 'react'
import {  useDispatch,useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import LazyLoad from 'react-lazyload';
import Spinner from '../../components/ui/spinner/spinner';
import * as actionType from '../../store/actions/action-type'
import getBlogs from '../../services/api/get-blog-items'
// import { Helmet } from "react-helmet";
import DocumentMeta from 'react-document-meta';

// importing UI component used
import SectionHeading from '../../components/pages-component/section-heading/section-heading'
import PageBanner from '../../components/ui/page-banner/page-banner';
import '../../components/pages-component/button/button.css'
import BlogItem from './components/blog-item/blog-item'
import deleteBlog from '../../services/api/delete-blog'
import pubunpubBlogs from '../../services/api/blog-unpublish'
import Modal from '../../components/ui/modal/modal'
import ShowMsg from '../../components/pages-component/showMSG/msg'
import Header from '../../components/layout/header/header';
import Footer from '../../components/layout/footer/footer';
import {getBlogs3,deleteBlog5,fetchBlogs3,pubunpubBlog3,deleteBlog6} from '../../actions/blogsActions'

const Blogs = (props) => {
  const dispatch = useDispatch() //this for excuting functions

  const [showModal, setshowModal] = useState(false)
  const [msg, setmsg] = useState(false)
  const [msgContent, setmsgContent] = useState('')
  const [attention, setattention] = useState(false)

  // const {getMedia2, onUpdateMediaItems2,onUpdateFirstRun2,mediaItems} = mediaContext;

  const {blogItems} = useSelector(state => state.blog)
  const {session} = useSelector(state =>state.login)

  const blogsCallback = (data) => {
    if (data.status === 200) {
      // props.fetchBlogs(data.data)
      dispatch(fetchBlogs3(data.data))

    }
  }

  useEffect(() => {
    window.scrollTo(0, 0);
    getBlogs3(blogsCallback)
    console.log(blogItems)
    // console.log(props.blog.blogItems)
  }, [])

  const unPublishCallback = (data) => {
    if (data.status === 200) {
      setmsg(true)
      setmsgContent('Successfully Changed')
      setshowModal(true)
      setattention(false)
    } else {
      setmsg(true)
      setmsgContent('Something Went Wrong')
      setshowModal(true)
      setattention(true)
    }
  };

  const publishBlog = (id) => {
    const data = {
      id: id,
      publish: true
    }
    pubunpubBlogs(unPublishCallback, id, { publish: true })
    // props.pubunpubBlog(data)
    dispatch(pubunpubBlog3(data))
  };

  const unpublishBlog = (id) => {
    const data = {
      id: id,
      publish: false
    }
    pubunpubBlogs(unPublishCallback, id, { publish: false })
    // props.pubunpubBlog(data)
    dispatch(pubunpubBlog3(data))
  };

  const deleteBlogCallback = (data) => {
    if (data.status === 204) {
      setmsg(true)
      setmsgContent('Successfully Deleted')
      setshowModal(true)
      setattention(false)
    } else {
      setmsg(true)
      setmsgContent('Something Went Wrong')
      setshowModal(true)
      setattention(true)
    }
  };

  const deleteBlog = (id) => {
    deleteBlog5(deleteBlogCallback, id)
    // props.deleteBlog(id)
    dispatch(deleteBlog6(id))
  };

  const closeModal = () => {
    setmsg(false)
    setmsgContent('')
    setshowModal(false)
    setattention(false)
  };

  // const blogsItem = props.blog.blogItems.map((item) => {
  //   return (
  //     <LazyLoad height={200} offset={100} placeholder={<Spinner />} once key={item.id}>
  //       <BlogItem
  //         title={item.title}
  //         date={item.created_at}
  //         id={item.id}
  //         imgUrl={item.thumbnail}
  //         publish={item.publish}
  //         publishBlog={publishBlog}
  //         unpublishBlog={unpublishBlog}
  //         deleteBlog={deleteBlog}
  //         session={props.session.session}
  //       />
  //     </LazyLoad>
  //   );
  // });
  const blogItemss = blogItems.map((item) => {
    return (
      <LazyLoad height={200} offset={100} placeholder={<Spinner />} once key={item.id}>
        <BlogItem
          title={item.title}
          date={item.created_at}
          id={item.id}
          imgUrl={item.thumbnail}
          publish={item.publish}
          publishBlog={publishBlog}
          unpublishBlog={unpublishBlog}
          deleteBlog={deleteBlog}
          session={session.session}
        />
      </LazyLoad>
    );
  });

  const meta = {
    title: 'Blogs – CNC Machine,3D printer |Ethereal Machines-Bangalore, India.',
    meta: {
      property: {
        'title': 'CNC Machine,3D printer |Ethereal Machines-Bangalore, India.',
        'description': 'Ethereal Machines – Blogs: simultaneous 5-axis CNC Machine & 3D Printer.',
      }
    }
  };

  return (
    <div className="page page--blogs-page">
      <Header />
      {/* <Helmet>
            <title>Blogs – CNC Machine,3D printer |Ethereal Machines-Bangalore, India.</title>
            <meta name="description" content="Ethereal Machines – Blogs: simultaneous 5-axis CNC Machine & 3D Printer."/>
            </Helmet> */}
      <DocumentMeta {...meta} />
      {
        msg
          ?
          <Modal show={showModal} clicked={closeModal}>
            <ShowMsg msg={msgContent} attention={attention} />
          </Modal>
          : null
      }
      <PageBanner heading={'Ethereal Machines Blogs'} subHeading={'Dream | Design | Create'} classValue={'page-banner--blogs-page'} />
      <section className="section section--blogs">

        <div className="container">
          {
            session.session
              ?
              <Link to="/blogs/create">
                <button className="form-btn">Create blog</button>
              </Link>
              :
              null
          }
        </div>
        <SectionHeading name={"Featured Blogs"} classValue={"u-margin-bottom-big u-text-center"} />
        <div className="blog-item-container three-col-layout">
          {/* {blogsItem} */}
          {blogItemss}
        </div>

      </section>
      <Footer />
    </div>
  );

}

